import PressButton from '../press-button/press-button.vue';

export default {
  components: {
    PressButton,
  },
  options: {
    styleIsolation: 'shared',
  },
  props: {
    title: {
      type: String,
      default: '',
    },
    body: {
      type: String,
      default: '',
    },
    classPrefix: {
      type: String,
      default: '',
    },
    steps: {
      type: Array,
      default: () => ([]),
    },
    skipButton: {
      type: Object,
      default: () => ({}),
    },
    backButton: {
      type: Object,
      default: () => ({}),
    },
    nextButton: {
      type: Object,
      default: () => ({}),
    },
    finishButton: {
      type: Object,
      default: () => ({}),
    },
    current: {
      type: Number,
      default: -1,
    },
    modeType: {
      type: String,
      default: '',
    },
  },
  methods: {
    clickButton(...args) {
      this.$emit('clickButton', ...args);
    },
  },
};
